import React, { useEffect, useRef, useState } from "react";
import { deepEqual } from "../../utils/array";
import SupplierProductListTwo from "./SupplierProductListTwo";
import FilterSideTwo from "../category/filterSidebar/filters/FilterSideTwo";
import market from "../../config/market";
import { useTranslation } from "react-i18next";
import { Splide, SplideSlide } from "@splidejs/react-splide";
import { sliderOptions } from "../../components/options";
import MediaImage from "../../components/features/product/MediaImage";
import YandexMapModal from "../../components/features/modal/YandexMapModalThree";
import { openModal } from "../../actions/modal";
import { useDispatch } from "react-redux";
import { useParams } from "react-router-dom";

const queryString = require("qs");
let timeOut = setTimeout(() => {}, 1000);
const KEEP_TIME = 1000;
const initialQuery = { page: 1, size: 20 };

function SupplierThree(props) {
	const [supplier, setSupplier] = useState();
	const { id: supplierId } = useParams();
	const [filter, setFilter] = useState(null);
	const [toggle, setToggle] = useState(false);
	const [isNumber, setIsNumber] = useState(false);
	const [count, setCount] = useState(0);
	const [query, setQuery] = useState(
		window.location.search.length > 0
			? queryString.parse(window.location.search.slice(1))
			: initialQuery
	);
	const { t } = useTranslation();

	//dispatch hook
	const dispatch = useDispatch();

	function resizeHandle() {
		if (window.document.querySelector("body")?.offsetWidth < 992)
			setToggle(true);
		else setToggle(false);
	}

	useEffect(() => {
		window.addEventListener("resize", resizeHandle);
		resizeHandle();

		return () => {
			window.removeEventListener("resize", resizeHandle);
		};
	}, []);
	// console.log(count);

	const myRef = useRef(null);
	const executeScroll = () =>
		myRef.current.scrollIntoView({ behavior: "smooth" });

	function hideSideBar() {
		if (
			document
				.querySelector("body")
				.classList.contains("sidebar-filter-active")
		)
			document
				.querySelector("body")
				.classList.remove("sidebar-filter-active");
	}
	function toggleSideBar() {
		if (
			document
				.querySelector("body")
				.classList.contains("sidebar-filter-active")
		)
			document
				.querySelector("body")
				.classList.remove("sidebar-filter-active");
		else
			document
				.querySelector("body")
				.classList.add("sidebar-filter-active");
	}

	//Open yandex map modal
	function openMapModal(e) {
		e.preventDefault();
		dispatch(openModal("map"));
	}

	function scrollToCompany() {
		window.scrollTo(
			0,
			document.querySelector("#about-company").getBoundingClientRect().y
		);
	}

	return (
		<div className="main supplier-page-three" ref={myRef}>
			<div className="page-content">
				{supplier?.images?.length > 0 ? (
					<div className="slider-supplier">
						<div className="d-flex">
							<Splide
								className="splide"
								options={{
									sliderOptions,
									pagination: true,
									arrows: false
								}}
							>
								{supplier?.images?.map((item, index) => {
									return (
										<SplideSlide key={index}>
											<div className="img-block">
												<MediaImage
													image={item}
													largeSize="original"
													mediumSize="800x_"
													smallSize="800x_"
													alt="slide-img"
												/>
											</div>
										</SplideSlide>
									);
								})}
							</Splide>
						</div>
					</div>
				) : null}

				<div className="info-supplier container">
					<div className="d-flex logo-content">
						{supplier && (
							<div
								className={`logo ${
									supplier.logo?.urls["300x_"]
										? `only-desktop`
										: `d-none`
								}`}
							>
								<MediaImage
									image={supplier.logo}
									largeSize="300x_"
									mediumSize="300x_"
									smallSize="150x_"
									alt="logo"
								/>
							</div>
						)}

						<div className="contanet-info">
							<h3 className="supplier-name only-desktop">
								<b>{supplier?.name}</b>
							</h3>
							<div className="row">
								{supplier && (
									<div className="col-12 only-mobile">
										<div
											className="logo"
											style={
												supplier?.logo?.urls["300x_"]
													? { display: "block" }
													: { display: "none" }
											}
										>
											<MediaImage
												image={supplier?.logo}
												largeSize="300x_"
												mediumSize="300x_"
												smallSize="150x_"
												alt="logo"
											/>
										</div>

										<h3 className="supplier-name">
											<b>{supplier?.name}</b>
										</h3>
									</div>
								)}
								<div className="col-md-8 col-12">
									<div className="title-description">
										<p>{supplier?.description}</p>
									</div>
								</div>
								<div className="offset-md-1 col-md-3 col-12">
									<div className="icon-descript">
										{supplier?.address?.length > 0 && (
											<p
												onClick={openMapModal}
												className="img-text d-flex align-items-start address-pointer"
											>
												<img
													src="assets/images/vibo/location.svg"
													alt="location"
												/>
												{supplier?.address}
											</p>
										)}
										{supplier?.phone && (
											<p className="img-text d-flex align-items-start">
												<img
													src="assets/images/vibo/phone.svg"
													alt="phone"
												/>
												{isNumber ? (
													<a
														href={`tel:${supplier?.phone}`}
														className="number"
														onClick={() =>
															setCount(count + 1)
														}
													>
														{supplier?.phone}
													</a>
												) : (
													<button
														className="btn show-number"
														onClick={() =>
															setIsNumber(
																!isNumber
															)
														}
													>
														{t("show_number")}
													</button>
												)}
											</p>
										)}
										{supplier && supplier.webSite && (
											<a
												href={supplier.webSite}
												className="img-text text-dark d-flex align-items-start"
											>
												<img
													src="assets/images/vibo/globe.svg"
													alt="globe"
												/>
												{supplier.webSite}
											</a>
										)}
										{supplier?.license && (
											<a
												href={supplier.license?.url}
												download
												className="img-text d-flex align-items-start"
											>
												<img
													src="assets/images/vibo/clipboard.svg"
													alt="clipboard"
												/>
												{t("license")}
											</a>
										)}
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
				<div className="tab-content-header">
					<div className="container">
						<div>{t("products_two")}</div>
						<div onClick={() => scrollToCompany()}>
							{t("about_company")}
						</div>
					</div>
				</div>
				<div className="container">
					<div className="row">
						<div className="col-lg-3 skeleton-body skel-shop-sidebar loaded">
							{filter ? (
								<FilterSideTwo
									onSubmit={values => {
										clearTimeout(timeOut);
										timeOut = setTimeout(() => {
											if (!deepEqual(query, values)) {
												setQuery(prevQuery => ({
													...prevQuery,
													...values,
													page: 1
												}));
											}
										}, KEEP_TIME);
									}}
									clearAll={() => {
										setQuery(initialQuery);
									}}
									filter={filter}
									adClass="sidebar sidebar-shop"
									executeScroll={executeScroll}
									initialValues={query}
									hideSideBar={hideSideBar}
								/>
							) : null}

							{toggle ? (
								<button
									className={`sidebar-fixed-toggler ${
										supplier ? `d-flex` : `d-none`
									}`}
									onClick={toggleSideBar}
								>
									{market.config.home === 6 ? (
										<span className="d-none">
											{t("filter")}
										</span>
									) : null}
									<svg
										id="icon"
										width="20px"
										fill="white"
										viewBox="0 0 48 48"
										xmlns="http://www.w3.org/2000/svg"
									>
										<title />
										<path d="M47,12a2,2,0,0,0-2-2H24a2,2,0,0,0,0,4H45A2,2,0,0,0,47,12Z" />
										<path d="M3,14H8.35a6,6,0,1,0,0-4H3a2,2,0,0,0,0,4Zm11-4a2,2,0,1,1-2,2A2,2,0,0,1,14,10Z" />
										<path d="M45,22H37.65a6,6,0,1,0,0,4H45a2,2,0,0,0,0-4ZM32,26a2,2,0,1,1,2-2A2,2,0,0,1,32,26Z" />
										<path d="M22,22H3a2,2,0,0,0,0,4H22a2,2,0,0,0,0-4Z" />
										<path d="M45,34H28a2,2,0,0,0,0,4H45a2,2,0,0,0,0-4Z" />
										<path d="M18,30a6,6,0,0,0-5.65,4H3a2,2,0,0,0,0,4h9.35A6,6,0,1,0,18,30Zm0,8a2,2,0,1,1,2-2A2,2,0,0,1,18,38Z" />
									</svg>
								</button>
							) : null}
							<div
								className="sidebar-filter-overlay"
								onClick={hideSideBar}
							></div>
						</div>

						<div className="col-lg-9 skeleton-body skel-shop-products loaded">
							<SupplierProductListTwo
								setFilter={setFilter}
								setSupplier={setSupplier}
								query={query}
								setQuery={setQuery}
								supplierId={supplierId}
								column="4cols"
								executeScroll={executeScroll}
							/>
						</div>
					</div>
				</div>
				<div className="tab-content-header">
					<div id="about-company" className="container">
						<div>{t("about_company")}</div>
					</div>
				</div>
				<div
					className="container"
					dangerouslySetInnerHTML={{
						__html: supplier?.content
					}}
				></div>
			</div>

			{supplier?.address?.length > 0 ? (
				<YandexMapModal supplierAdress={supplier?.address} />
			) : null}
		</div>
	);
}

export default SupplierThree;
