import React, { useState } from "react";
import { useTranslation } from "react-i18next";
import market from "../../../../config/market";

function Toolbox({ setCols, pages, cols, setQuery, sortIcon }) {
	const { t } = useTranslation();
	const [activeVal, setActiveVal] = useState(t("titleAZ"));
	function changeFilter(e) {
		setQuery(prevQ => ({
			...prevQ,
			order: e.target.value
		}));
	}
	const [isOpen, setIsOpen] = useState(false);
	return (
		<div className="toolbox">
			<div className="toolbox-center">
				<div className="toolbox-info">
					{t("shown")}{" "}
					<span>
						{pages?.variations.length} {t("from")}{" "}
						{pages?.totalCount}
					</span>{" "}
					{t("products")}
				</div>
			</div>
			<div className="toolbox-right">
				<div className="toolbox-sort">
					{sortIcon ? <img src={sortIcon} alt="sort" /> : null}
					<label htmlFor="sortby">
						<span className="sort">{t("sort")}</span>&nbsp;
						<span className="by">по</span>:
					</label>
					<div className="select-custom">
						<form
							id="app-cover"
							onChange={e => {
								changeFilter(e);
							}}
						>
							<div
								id="select-box"
								className={`select-box ${
									isOpen ? `active` : ``
								}`}
							>
								<div
									id="select-button"
									onClick={() => setIsOpen(!isOpen)}
								>
									<div id="selected-value">
										<span>
											{activeVal}
											<img
												src={market.config.icons.rArrow}
												alt="selecticon"
											/>
										</span>
									</div>
								</div>
								<div id="options">
									<div className="options">
										<div className="option">
											<input
												className="s-c top"
												type="radio"
												name="filter"
												value="name asc"
												onClick={() => {
													setActiveVal(t("titleAZ"));
													setIsOpen(!isOpen);
												}}
											/>
											<span className="label">
												{t("titleAZ")}{" "}
												<img
													src={
														market.config.icons
															.rArrow
													}
													alt="selecticon"
												/>
											</span>
										</div>
										<div className="option">
											<input
												className="s-c top"
												type="radio"
												name="filter"
												value="name desc"
												onClick={() => {
													setActiveVal(t("titleZA"));
													setIsOpen(!isOpen);
												}}
											/>

											<span className="label">
												{t("titleZA")}{" "}
												<img
													src={
														market.config.icons
															.rArrow
													}
													alt="selecticon"
												/>
											</span>
										</div>

										<div className="option">
											<input
												className="s-c top"
												type="radio"
												name="filter"
												value="sellPrice asc"
												onClick={() => {
													setActiveVal(t("cheaper"));
													setIsOpen(!isOpen);
												}}
											/>

											<span className="label">
												{t("cheaper")}{" "}
												<img
													src={
														market.config.icons
															.rArrow
													}
													alt="selecticon"
												/>
											</span>
										</div>
										<div className="option">
											<input
												className="s-c top"
												type="radio"
												name="filter"
												value="sellPrice desc"
												onClick={() => {
													setActiveVal(
														t("expensive")
													);
													setIsOpen(!isOpen);
												}}
											/>
											<span className="label">
												{t("expensive")}{" "}
												<img
													src={
														market.config.icons
															.rArrow
													}
													alt="selecticon"
												/>
											</span>
										</div>
									</div>
								</div>
							</div>
						</form>
					</div>
				</div>
			</div>
		</div>
	);
}

export default Toolbox;
