import React, { useRef, useState } from "react";
import { deepEqual } from "../../utils/array";
import FilterSideTwo from "../category/filterSidebar/filters/FilterSideTwo";
import Company from "./supplier-header/info-company-two";
import SupplierProductList from "./SupplierProductList";

const queryString = require("qs");
let timeOut = setTimeout(() => {}, 1000);
const KEEP_TIME = 1000;
const initialQuery = { page: 1, size: 12 };

function SupplierTwo(props) {
	const [supplier, setSupplier] = useState();
	const supplierId = props.match.params.id;
	const [filter, setFilter] = useState(null);
	const [query, setQuery] = useState(
		window.location.search.length > 0
			? queryString.parse(window.location.search.slice(1))
			: initialQuery
	);
	const myRef = useRef(null);
	const executeScroll = () =>
		myRef.current.scrollIntoView({ behavior: "smooth" });

	function hideSideBar() {
		if (
			document
				.querySelector("body")
				.classList.contains("sidebar-filter-active")
		)
			document
				.querySelector("body")
				.classList.remove("sidebar-filter-active");
	}

	function toggleSideBar() {
		if (
			document
				.querySelector("body")
				.classList.contains("sidebar-filter-active")
		)
			document
				.querySelector("body")
				.classList.remove("sidebar-filter-active");
		else
			document
				.querySelector("body")
				.classList.add("sidebar-filter-active");
	}

	return (
		<>
			<div className="main supplier-page" ref={myRef}>
				<div className="page-content">
					<div className="container">
						<div className="row">
							<div className="col-lg-4 skeleton-body skel-shop-sidebar loaded">
								<Company supplier={supplier} />
								{filter ? (
									<FilterSideTwo
										onSubmit={values => {
											clearTimeout(timeOut);
											timeOut = setTimeout(() => {
												if (!deepEqual(query, values)) {
													setQuery(prevQuery => ({
														...prevQuery,
														...values,
														page: 1
													}));
												}
											}, KEEP_TIME);
										}}
										clearAll={() => {
											setQuery(initialQuery);
										}}
										filter={filter}
										adClass="sidebar sidebar-shop"
										executeScroll={executeScroll}
										initialValues={query}
										hideSideBar={hideSideBar}
									/>
								) : null}
							</div>
							<div className="col-lg-8 skeleton-body skel-shop-products loaded">
								<SupplierProductList
									toggleSideBar={toggleSideBar}
									setFilter={setFilter}
									setSupplier={setSupplier}
									query={query}
									setQuery={setQuery}
									supplierId={supplierId}
									column="4cols"
									executeScroll={executeScroll}
								/>
							</div>
						</div>
					</div>
				</div>
			</div>
		</>
	);
}

export default SupplierTwo;
