import React, { useState, useEffect, Fragment } from "react";
import { useDispatch } from "react-redux";
import { setStatePages } from "../../actions/any";
import ToolboxThree from "../../components/features/product/details/ToolboxThree";
import ProductCard from "../../components/features/product";
import ProductCardRow from "../../components/features/product/ProductCardRow";
import QuickView from "../../components/features/product/QuickViewNew";
import ChooseVariationModal from "../../components/features/modal/choose-variation";
import supplierProducts from "../../api/supplierProducts";
import SimpleShimmerProducts from "../../components/features/loaders/SimpleShimmerProducts";
import Pagination from "../../components/features/product/details/Pagination";
import { useTranslation } from "react-i18next";
// import { getProductsPromotions } from "../../api/products";

const queryString = require("qs");
const initialPages = {
	page: 1,
	totalCount: 0,
	variations: [],
	hasMore: true,
};

function SupplierProductList({
	supplierId,
	title,
	query,
	setQuery,
	column,
	setSupplier,
	filter,
	setFilter,
}) {
	const dispatch = useDispatch();
	const [cols, setCols] = useState(column);
	const [loading, setLoading] = useState(true);
	const [pages, setPages] = useState(initialPages);
	const [modal, setModal] = useState({
		open: false,
		product: { variation: { id: 1 } },
	});

	const { t } = useTranslation();
	function toTop() {
		window.scroll({
			top: 0,
		});
	}
	useEffect(() => {
		setLoading(true);
		const strQuery = queryString.stringify({ ...query });
		supplierProducts(supplierId, strQuery)
			.then((page) => {
				setSupplier(page.supplier);

				setFilter({ ...filter, ...page.filter });

				window.history.replaceState(
					null,
					title,
					`/supplier/${supplierId}?` + strQuery
				);

				setPages((prevPage) => ({
					page: prevPage.page,
					totalCount: page.totalCount,
					variations: page.variations,
					hasMore: page.variations.length > 0,
				}));
				setLoading(false);
			})
			.catch((err) => {
				setPages((prevPage) => ({
					...prevPage,
					hasMore: false,
				}));
				setLoading(false);
			});
	}, [query]);

	function onAddToCart(product) {
		setModal({
			open: true,
			product: product,
		});
	}

	useEffect(() => {
		if (pages.variations.length > 0) {
			// const ids = pages.variations.reduce((pv, nv) => {
			// 	return [...pv, nv.variation.id];
			// }, []);
			// getProductsPromotions(ids).then(res => {
			// 	if (res.length) {
			// 		setPages({
			// 			...pages,
			// 			variations: pages.variations.map(variation => {
			// 				return {
			// 					...variation,
			// 					...res.find(
			// 						v =>
			// 							v.variationId === variation.variation.id
			// 					)
			// 				};
			// 			})
			// 		});
			// 	}
			// });
			let p = [];
			pages.variations.map((page) =>
				p.push({
					id: page.variation.id,
					name: page.variation.name,
					image: page.variation.images[0]?.urls?.original,
				})
			);
			dispatch(setStatePages(p));
		}
	}, [pages.variations]);

	return (
		<>
			<div className="product-toolbox">
				<ToolboxThree
					setCols={setCols}
					setQuery={setQuery}
					cols={cols}
					pages={pages}
				/>
			</div>
			<h2 className="category-title">{title}</h2>
			{/* <div className={loading ? "d-none" : "w-100 p-5"}>
				<Toolbox setQuery={setQuery} cols={cols} pages={pages} />
			</div> */}

			{loading ? (
				<div className="products mb-3">
					<SimpleShimmerProducts />
				</div>
			) : (
				<div className="products mb-3">
					{pages.variations.length === 0 ? (
						<div className="row">
							<h4>{t("nothing-found")}</h4>
						</div>
					) : (
						<Fragment>
							<div className="row">
								{pages.variations.map((product, i) =>
									cols === "12cols" ? (
										<div className="col-12">
											<ProductCardRow
												haveSupplier={true}
												key={i}
												product={product}
												type="sidebar"
												onAddToCart={(dataProduct) => {
													onAddToCart(dataProduct);
												}}
											/>
										</div>
									) : (
										<div className="col-md-3 col-6" key={i}>
											<ProductCard
												product={product}
												type="sidebar"
												onAddToCart={(dataProduct) => {
													onAddToCart(dataProduct);
												}}
											/>
										</div>
									)
								)}
							</div>

							<QuickView />
							<ChooseVariationModal
								setModal={setModal}
								modal={modal}
							/>
						</Fragment>
					)}
				</div>
			)}
			{pages.totalCount > 1 ? (
				<div className="page-pagination">
					<Pagination
						pageCount={Math.ceil(pages.totalCount / 20)}
						onChange={({ selected }) => {
							setQuery((prev) => ({
								...prev,
								page: 1 + selected,
							}));
							toTop();
						}}
					/>
				</div>
			) : null}
		</>
	);
}

export default SupplierProductList;
