import React from "react";
import market from "../../config/market";
import SupplierOne from "./SupplierOne";
import SupplierTwo from "./SupplierTwo";
import SupplierThree from "./SupplierThree";
import SupplierFour from "./SupplierFour";

export default function SupplierPage(props) {
	switch (market.config.supplierPage) {
		case 1:
			return <SupplierOne {...props} />;
		case 2:
			return <SupplierTwo {...props} />;
		case 3:
			return <SupplierThree {...props} />;
		case 4:
			return <SupplierFour {...props} />;
		default:
			return <SupplierOne {...props} />;
	}
}
