import React, { useState, useEffect, Fragment } from "react";
import { connect } from "react-redux";
import InfiniteScroll from "react-infinite-scroll-component";
import store from "../../store";
import { setStatePages } from "../../actions/any";
import ProductCard from "../../components/features/product/";
import QuickView from "../../components/features/product/QuickViewNew";
import { addToCart } from "../../actions/cart";
import ToolboxThree from "../../components/features/product/details/ToolboxThree";
import ChooseVariationModal from "../../components/features/modal/choose-variation";
import LoadMoreButton from "../../components/features/product/details/loadMoreButton";
import supplierProducts from "../../api/supplierProducts";
import SimpleShimmerProducts from "../../components/features/loaders/SimpleShimmerProducts";
import { useTranslation } from "react-i18next";
import { getImageUrl } from "../../utils/oxMethods";
import market from "../../config/market";
import Toolbox from "../../components/features/product/details/toolbox-two";
import ToolboxFive from "../../components/features/product/details/toolbox-five";
import Pagination from "../../components/features/product/details/Pagination";
// import { getProductsPromotions } from "../../api/products";

const MORE_LOAD_PAGE_STEP = 5;
const grid = {
	"2cols": "col-6",
	"3cols": "col-6 col-md-4 col-lg-4",
	"4cols": "col-6 col-md-4 col-lg-4 col-xl-3",
	"5cols": "col-6 col-md-4 col-lg-3 col-xl-5col",
};
const queryString = require("qs");
const initialPages = {
	page: 1,
	totalCount: 0,
	variations: [],
	hasMore: true,
	allPages: 1,
};
function SupplierProductList({
	supplierId,
	addToCart,
	setFilterOnce,
	filterOnce,
	title,
	query,
	setFilter,
	setQuery,
	column,
	setSupplier,
	toggleSideBar,
}) {
	// const [fixedFilter, setFixedFilter] = useState({});
	const [cols, setCols] = useState(column);
	const [loading, setLoading] = useState(true);
	const [pages, setPages] = useState(initialPages);
	const [modal, setModal] = useState({
		open: false,
		product: { variation: { id: 1 } },
	});
	const { t } = useTranslation();

	// useEffect(() => {
	// 	window.addEventListener("scroll", () => {
	// 		if (window.scrollY > 150) {
	// 			setFixedFilter({
	// 				position: "fixed",
	// 				top: "0",
	// 				left: "0",
	// 				right: "0",
	// 				zIndex: "9999"
	// 			});
	// 		} else {
	// 			setFixedFilter({
	// 				position: "relative"
	// 			});
	// 		}
	// 	});
	// }, [window.scrollY]);
	function toTop() {
		window.scroll({
			top: 0,
		});
	}

	let mount = true;
	async function getSuplierProducts(query, more) {
		const strQuery = queryString.stringify(query);
		supplierProducts(supplierId, strQuery)
			.then((page) => {
				setSupplier(page.supplier);
				if (!filterOnce && Object.keys(page.filter).length > 0) {
					window.localStorage.setItem(
						"supplier-filter",
						JSON.stringify(page.filter)
					);

					setFilter(
						JSON.parse(
							window.localStorage.getItem("supplier-filter")
						)
					);
					setFilterOnce(true);
				}

				window.history.replaceState(
					null,
					title,
					`/supplier/${supplierId}?` + strQuery
				);
				if (mount) {
					setPages((prevPage) => ({
						page: page.page,
						totalCount: page.totalCount,
						variations: more
							? [...prevPage.variations, ...page.variations]
							: page.variations,

						hasMore: page.variations.length > 0,
						allPages: Math.ceil(page.totalCount / query.size),
					}));
					setLoading(false);
				}
			})
			.catch((err) => {
				// window.location = process.env.PUBLIC_URL + "pages/404";
				setPages((prevPage) => ({
					...prevPage,
					hasMore: false,
				}));
				setLoading(false);
			});
	}

	// useEffect(() => {
	// 	setLoading(true);

	// }, [query]);

	function fetchMoreData(load_more = false) {
		if (pages.totalCount === pages.variations.length) {
			setPages((prevPage) => ({
				...prevPage,
				more_load_time: false,
				hasMore: false,
			}));
			return;
		}
		if (!load_more && pages.page % MORE_LOAD_PAGE_STEP === 0) {
			setPages((prevPage) => ({
				...prevPage,
				hasMore: false,
				more_load_time: true,
			}));
			return;
		}

		if (query.page <= pages.allPages) {
			query.page = parseInt(query.page) + 1;
		}
		// const strQuery = queryString.stringify({ query });
		getSuplierProducts(query, true);
	}

	function onAddToCart(product) {
		setModal({
			open: true,
			product: product,
		});
	}
	function ToggleSideBar() {
		if (toggleSideBar) {
			return toggleSideBar();
		}
		return window.document
			.querySelector("body")
			.classList.add("sidebar-filter-active");
	}

	useEffect(() => {
		setLoading(true);
		getSuplierProducts(query, false);
		toTop();
		return () => {
			mount = false;
		};
	}, [query, supplierId]);

	useEffect(() => {
		if (pages.variations.length > 0) {
			let p = [];
			pages.variations.map((page) =>
				p.push({
					id: page.variation.id,
					name: page.variation.name,
					image: getImageUrl(page.variation.images[0], "300x_"),
				})
			);
			store.dispatch(setStatePages(p));
		}
	}, [pages.variations]);

	return (
		<>
			<h2 className="category-title">{title}</h2>
			<div
				className="category-toolbox"
				style={{ height: "50px", marginBottom: "25px" }}
			>
				<div
					className="w-100 row p-3 m-auto filter-toolbox-block"
					// style={fixedFilter}
				>
					<div className="filter-b col-6 text-center align-self-center">
						<div
							className="text-primary d-flex text-center justify-content-center"
							onClick={() => ToggleSideBar()}
							style={{ cursor: "pointer" }}
						>
							<svg
								id="icon-primary"
								width="15px"
								className="mr-2"
								viewBox="0 0 48 48"
								xmlns="http://www.w3.org/2000/svg"
							>
								<title />
								<path d="M47,12a2,2,0,0,0-2-2H24a2,2,0,0,0,0,4H45A2,2,0,0,0,47,12Z" />
								<path d="M3,14H8.35a6,6,0,1,0,0-4H3a2,2,0,0,0,0,4Zm11-4a2,2,0,1,1-2,2A2,2,0,0,1,14,10Z" />
								<path d="M45,22H37.65a6,6,0,1,0,0,4H45a2,2,0,0,0,0-4ZM32,26a2,2,0,1,1,2-2A2,2,0,0,1,32,26Z" />
								<path d="M22,22H3a2,2,0,0,0,0,4H22a2,2,0,0,0,0-4Z" />
								<path d="M45,34H28a2,2,0,0,0,0,4H45a2,2,0,0,0,0-4Z" />
								<path d="M18,30a6,6,0,0,0-5.65,4H3a2,2,0,0,0,0,4h9.35A6,6,0,1,0,18,30Zm0,8a2,2,0,1,1,2-2A2,2,0,0,1,18,38Z" />
							</svg>
							Фильтр
						</div>
					</div>
					<div className="sort-b col-6 text-center align-self-center">
						<ToolboxThree
							setCols={setCols}
							setQuery={setQuery}
							cols={cols}
							pages={pages}
						/>
					</div>
				</div>
			</div>
			<div className="filterr mt-2 d-none">
				<button
					className="filter toogler"
					onClick={() => ToggleSideBar()}
				>
					<img src={market.config.icons.filter} alt="filter" />
					<span className="ml-2">Фильтр</span>
				</button>
				{market.config.home === 8 ? (
					<ToolboxFive setQuery={setQuery} pages={pages} />
				) : (
					<Toolbox setQuery={setQuery} pages={pages} />
				)}
			</div>
			{loading ? (
				<div className="products mb-3">
					<div className="row">
						<SimpleShimmerProducts />
					</div>
				</div>
			) : (
				<div className="products mb-3">
					{pages.variations.length === 0 ? (
						<div className="row">
							<h4>{t("nothing-found")}</h4>
						</div>
					) : (
						<Fragment>
							<InfiniteScroll
								dataLength={pages.variations.length}
								next={fetchMoreData}
								hasMore={pages.hasMore}
								loader={
									<div className="w-100">
										<SimpleShimmerProducts />
									</div>
								}
								className="row"
							>
								{pages.variations.map((product, i) => (
									<div
										key={i}
										className={`${grid[cols]} d-flex`}
									>
										<ProductCard
											product={product}
											type="sidebar"
											onAddToCart={() => {
												onAddToCart(product);
											}}
										/>
									</div>
								))}
							</InfiniteScroll>
							{pages.allPages > 1 ? (
								<>
									<LoadMoreButton
										pages={pages}
										fetchMoreData={fetchMoreData}
									/>
									<div className="page-pagination">
										<Pagination
											pageCount={pages.allPages}
											onChange={({ selected }) => {
												setQuery((prev) => ({
													...prev,
													page: 1 + selected,
												}));
											}}
											forcePage={query.page}
										/>
									</div>
								</>
							) : null}
							<QuickView />
							<ChooseVariationModal
								setModal={setModal}
								modal={modal}
								addToCart={addToCart}
							/>
						</Fragment>
					)}
				</div>
			)}
		</>
	);
}

export default connect(null, { addToCart })(SupplierProductList);
