import React, { useEffect, useState } from "react";
import { deepEqual } from "../../utils/array";
import market from "../../config/market";
import { Splide, SplideSlide } from "@splidejs/react-splide";
import { sliderOptions } from "../../components/options";
import MediaImage from "../../components/features/product/MediaImage";
import { useParams } from "react-router-dom";
import ToolboxTwo from "../../components/features/product/details/toolbox-two";
import BackButton from "../../components/common/BackButton";
import { dt } from "../../i18n";
import SearchModal from "../../components/features/modal/SearchModal";
import FilterSideTwo from "../category/filterSidebar/filters/FilterSideTwo";
import SupplierProductListTwo from "./SupplierProductList";

const queryString = require("qs");
let timeOut = setTimeout(() => {}, 1000);
const KEEP_TIME = 1000;
const initialQuery = { page: 1, size: 20 };

function SupplierFour() {
	const [supplier, setSupplier] = useState();
	const [filterOnce, setFilterOnce] = useState(false);
	const { id: supplierId } = useParams();
	const [filter, setFilter] = useState(
		JSON.parse(window.localStorage.getItem("supplier-filter"))
	);
	const [search, setSearch] = useState(false);
	const [query, setQuery] = useState(
		window.location.search.length > 0
			? queryString.parse(window.location.search.slice(1))
			: initialQuery
	); //dispatch hook

	function resizeHandle() {
		if (window.document.querySelector("body")?.offsetWidth < 992) {
			window.document.querySelector("header").classList.add("d-none");
		} else {
			window.document.querySelector("header").classList.remove("d-none");
		}
	}

	useEffect(() => {
		window.addEventListener("resize", resizeHandle);
		resizeHandle();

		return () => {
			window.removeEventListener("resize", resizeHandle);
		};
	}, []);

	function hideSideBar() {
		if (
			document
				.querySelector("body")
				.classList.contains("sidebar-filter-active")
		)
			document
				.querySelector("body")
				.classList.remove("sidebar-filter-active");
	}
	function toggleSideBar() {
		if (
			document
				.querySelector("body")
				.classList.contains("sidebar-filter-active")
		)
			document
				.querySelector("body")
				.classList.remove("sidebar-filter-active");
		else
			document
				.querySelector("body")
				.classList.add("sidebar-filter-active");
	}

	return (
		<div className="main supplier-page-four">
			<div className="supplier-header">
				<div className={"d-flex align-items-center"}>
					<BackButton icon={market.config.icons.backArrow} />
				</div>
				<h2 className="category-title only-mobile">
					{dt(supplier?.name)}
				</h2>
				<button
					className="btn-search"
					onClick={() => setSearch(!search)}
				>
					<img src={market.config.icons.search} alt="search" />
				</button>
			</div>
			<div className="page-content">
				<div className="info-supplier container">
					{supplier?.images?.length > 0 ? (
						<div className="slider-supplier">
							<Splide
								className="splide"
								options={{
									sliderOptions,
									pagination: true,
									arrows: false
								}}
							>
								{supplier?.images?.map((item, index) => {
									return (
										<SplideSlide key={index}>
											<div className="img-block">
												<MediaImage
													image={item}
													largeSize="800x_"
													mediumSize="800x_"
													smallSize="800x_"
													alt="slide-img"
												/>
											</div>
										</SplideSlide>
									);
								})}
							</Splide>
						</div>
					) : null}
					<div className="d-flex logo-content">
						<div className="contanet-info">
							<div className="row">
								{supplier && (
									<div className="col-12">
										<div
											className="logo"
											style={
												supplier?.logo?.urls["300x_"]
													? {
															display: "block"
													  }
													: {
															display: "none"
													  }
											}
										>
											<MediaImage
												image={supplier?.logo}
												largeSize="300x_"
												mediumSize="300x_"
												smallSize="150x_"
												alt="logo"
											/>
										</div>
										<div className="title-description">
											<h3 className="supplier-name">
												{supplier?.name}
											</h3>
											<div className="title-description">
												<p>{supplier?.description}</p>
											</div>
										</div>
									</div>
								)}
							</div>
						</div>
					</div>
				</div>
				<div className="container">
					<div className="row">
						<div className="col-12">
							<div className="filter-toolbox-block">
								<div className="filter-b text-center align-self-center only-mobile">
									<div
										className="d-flex text-center justify-content-center"
										onClick={() => toggleSideBar()}
										style={{ cursor: "pointer" }}
									>
										<img
											src={market.config.icons.filter}
											alt="filter"
										/>
										Фильтр
									</div>
								</div>
								<div className="sort-b text-center align-self-center">
									<ToolboxTwo
										setQuery={setQuery}
										sortIcon={market.config.icons.sort}
									/>
								</div>
							</div>
						</div>
					</div>
					<div className="row">
						<div className="col-lg-3 skeleton-body skel-shop-sidebar loaded">
							{filter ? (
								<FilterSideTwo
									onSubmit={values => {
										clearTimeout(timeOut);
										timeOut = setTimeout(() => {
											if (!deepEqual(query, values)) {
												setQuery(prevQuery => ({
													...prevQuery,
													...values,
													page: 1
												}));
											}
										}, KEEP_TIME);
									}}
									clearAll={() => {
										setQuery(initialQuery);
									}}
									filter={filter}
									adClass="sidebar sidebar-shop"
									initialValues={query}
									hideSideBar={hideSideBar}
								/>
							) : null}
						</div>

						<div className="col-lg-9 skeleton-body skel-shop-products loaded">
							<SupplierProductListTwo
								setFilter={setFilter}
								setSupplier={setSupplier}
								setFilterOnce={setFilterOnce}
								filterOnce={filterOnce}
								query={query}
								setQuery={setQuery}
								supplierId={supplierId}
								column="4cols"
							/>
						</div>
					</div>
				</div>
			</div>
			<SearchModal search={search} setSearch={setSearch} />
		</div>
	);
}

export default SupplierFour;
